.catalog-tags-link{
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 20px;
	a{
		font-size: 14px;
		line-height: 26px;
		color: #000000;
		padding: 5px 15px;
		border: 1px solid #CFD5D5;
		box-sizing: border-box;
		border-radius: 2px;
		margin-right: 13px;
		margin-bottom: 13px;
		transition: .3s;
		&:hover{
			border-color: #9c9c9c;
		}
	}
}